* {
    margin: 0;
    padding: 0;
    font-family: Poppins, sans-serif;
    font-size: 24px; 
}

html {
    overflow-y: overlay;
}

body {
    background-color: #fff;
}

a {
    text-decoration: none;
}
@media screen and (max-width: 1366px) {
    body p, i, b {
        font-size: 20px;
    }
    body h1 {
        font-size: 30px;
        /* font-weight: 100; */
    }
    body h2 {
        font-size: 26px;
    }
    body code {
        font-size: 21px;
    }
    body a {
        font-size: 21px;
    }
    body button {
        font-size: 21px;
    }

    p.safe-title {
        font-size: 40px;
    }

    p.safe-undertitle {
        font-size: 25px;
    }

    p.safe-community-title {
        font-size: 16px;
        /* color: gray; */
    }

    div.titles {
        background-color: antiquewhite;
        padding: 10px;
        border-radius: 8px;
        margin-top: 35px;
        margin-bottom: 20px;
    }

    div.refresh-status-div {
        flex-direction: row;
        justify-content: space-around;
        height: 30px;
        width: 90%;
        margin: 0px 15px 15px;
    }

    div.refresh-status-div .refresh-div {
       margin: 0px 15px;
    }
    
    div.refresh-status-div .status {
        margin: 0px 15px;
    }

    div.refresh-status-div .refresh-div p {
        font-size: 14px;
    }

    div.refresh-status-div .status p {
        font-size: 14px;
    }

    div.counters-div div.counter {
        padding: 5px 10px;
        min-width: 250px;
        min-height: 175px;
    }

    div.app {
        grid-gap: 5px;
        gap: 5px;
    }

    .app {
        padding: 6%
    }

    .titles {
        max-width: 33%;
    }

    .emaid-color {
        margin-bottom: 12px;
    }
}

