.app {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 65vh;
    grid-gap: 35px;
    gap: 35px;
    padding-top: 5%
    
}

.titles {
    display: flex;
    flex-direction: column;
    flex: 2 1;
    flex-wrap: wrap;
    text-align: center;
    justify-content: center;
    margin: 25px;
    max-width: 66%;
}

.titles p {
    margin: 5px;
}

.safe-title {
    color:#ff3355;
    font-size: 60px;
}

.safe-undertitle {
    color: #ff3355;
}

.safe-community-title {
    /* color: #000000; */
    color: #26264c;
}

.counters-div {
    background-color: #f5f4f2;
    display: flex;
    flex-direction: row;
    flex: 5 1;
    flex-wrap: wrap;
    text-align: center;
    justify-content: center;
    margin-top: 10px;
    margin-bottom: 25px;
}

.counter {
    display: flex;
    flex-direction: column;
    flex: 2 1;
    justify-content: center;
    min-width: 250px;
    min-height: 125px;
    padding: 25px 50px;
    border: black dotted 1px; 
    box-shadow: rgb(0,0,0,0.2) 5px 5px;
    margin: 5px 5px;    
}

.emaid {
    background-color: #f8f8fa; 
    color: #8994a3;
}

.emaid-pending {
    background-color: #26264c; 
    color: #f5f4f0;
}

.omaid {
    background-color: #8994a3; 
    color: #FFF;
}

.combined {
    background-color: #ff3355; 
    color: #43423d;
}

.emaid-color {
    color: #f05825; 
}

.omaid-color {
    color: #5fa3f2; 
}

.btc {
    background-color: orange;
    color: black;
}

.external-links-div {
    display: flex;
    flex-direction: row;
    flex: 1 1;
    flex-wrap: wrap;
    text-align: center;
    justify-content: center;
    align-items: center;
    padding: 10px;
}
    

.external-links a {
    background-color: #5fa3f2;
    color: white;
    border-radius: 25px;
    margin: 10px;
    padding: 5px;
    min-width: 33%;
}

.refresh-status-div {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    height: 50px;
    width: 1200px;
    padding: 10px 35px;
    margin-bottom: 25px;
}

.refresh-div {
    display: flex;
    flex-direction: row;
    flex: 2 1;
    flex-wrap: wrap;
    justify-content: space-around;
    align-content: center;
    background-color: black;
    color: white;
    padding: 15px;
    border-radius: 15px;
    height: 100%;
    margin: 15px;
}

.refresh-div p, button{
    font-size: 16px;
    font-weight: 1000;
}

.status {
    display:flex;
    flex-wrap: wrap;
    flex: 4 1;
    justify-content: center;
    align-content: center;
    padding: 15px;
    border-radius: 15px;
    height: 100%;
}

.status p {
    font-size: 16px;
    font-weight: 1000;
}

.api-urls-div {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

.api-urls-div h1 {
    margin-bottom: 5px;
}

.api-urls-div p {
    margin-bottom: 10px;
    color:gray;
}

.api-urls-div ul {
    margin-left: 25px;
}

.api-urls-div b,code {
    overflow-wrap: anywhere;
}

.emaid-color {
    margin-bottom: 12px;
}

.footer {
    color: #26264c; /* replace with your website's primary color code */
    background-color: #FFF; /* replace with your website's background color */
    padding: 20px;
    text-align: left; /* or left, if you prefer */
}

.footer li {
    margin: 10px 0;
}

.footer ul {
    list-style: none; /* Uncomment this line to remove default list styling */
    padding: 0;
    margin: 0; /* It's good practice to reset default margins for lists */
}

.footer a {
    color: #ff3355; /* replace with your preferred link color */
    /* text-decoration: none; */
    transition: color 0.3s ease;
}

.footer a:hover {
    color: #ff3355; /* replace with the color when hovering */
    text-decoration: underline; /* or you can change to 'none' */
}

/* If using icons */
.footer i {
    margin-right: 5px;
}

.footer-b p {
    color: #8994a3; /* replace with your preferred link color */
    text-decoration: none;
    transition: color 0.3s ease;
}

.contact-div {
    display: flex;
    flex-direction: column;
    align-items: left; /* Center horizontally */
    justify-content: center; /* Center vertically */
    text-align: left;
    flex: 1 1;
    max-width: 100%;
}

.contact-div i {
    font-size: 14px;
    color: #ff3355;
}

.refresh-status-div.flash {
    animation: flash 0.2s infinite alternate ease-in-out;
}

@keyframes flash {
    from {
        opacity: 1;
    }
    to {
        opacity: 0.5;
    }
}
.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 50px; /* Updated height to 30px */
  background-color: #26264c;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px; /* Adjusted padding */
  color: #FFF;
  z-index: 1000;
}

.links {
  display: flex;
  align-items: center;
  width: 50%; /* External links take up the first 50% */
}

.links a {
  color: white; /* Set link color to white */
  text-decoration: none;
  margin-right: 25px;
  font-family: Poppins, sans-serif;
}

.ticker-container {
  display: flex;
  align-items: right;
  justify-content: right;
  width: 50%; /* Set your desired width */
}

.hmove { 
  display: flex; 
}
.hitem { 
  width: 100%; flex-shrink: 0; 
}
.hwrap {
  width: 40%;
  overflow: hidden; 
}

@keyframes tickerh {
  0% { transform: translatex(100%); }
  100% { transform: translatex(-200%); }
}
.hmove { 
  animation: tickerh linear 15s infinite; 
}
.hmove:hover {
  animation-play-state: paused;
}

.ticker-symbol {
  font-size: 12px;
}

.ticker-value {
  font-size: 15px;
}

p b {
  font-size: 17px;
}

